import { Button, Col, Form, Input, Row } from 'antd';
import React, { useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';

export default function OpeningShiftPage() {
	const dispatch = useDispatch();
	const history = useHistory();

	const app = JSON.parse(localStorage.getItem('app') || '{}');

	const emailRef = useRef(null);
	const passwordRef = useRef(null);
	const openingRef = useRef(null);
	const [form] = Form.useForm();
	const { loading = false, isGrantedAccess = false } = useSelector((state) => state?.auth);
	const { loading: loadingTrx = false } = useSelector((state) => state?.transaction);
	const { property = {}, cashier = {} } = app;

	const onFinish = (values) => {
		if (isGrantedAccess) {
			handleOpeningShift(values);
			return;
		}

		dispatch({
			type: 'auth/USER_ACCESS',
			payload: {
				email: values?.email,
				password: window.btoa(values?.password),
				funcId: '1038'
			},
			history: () => {}
			// history: () => {
			// 	dispatch({
			// 		type: 'auth/LOGOUT_POS',
			// 		history
			// 	});
			// }
		});

		dispatch({
			type: 'auth/property'
		});
	};

	const onFinishFailed = (e) => console.log('Error! ', e);

	const onKeyDown = (e) => {
		if (isGrantedAccess) {
			if (e.key === 'Escape') {
				openingRef.current.blur();
				openingRef.current.value = '0';
			}

			openingRef && openingRef.current && openingRef.current.focus();
			return;
		} else {
			const isActiveEmail = document.activeElement === (emailRef.current !== null && emailRef.current.input) || false;
			const isActivePassword = document.activeElement === (passwordRef.current !== null && passwordRef.current.input) || false;

			if (e.key === 'Enter' && isActiveEmail) {
				emailRef.current.blur();
				passwordRef.current.focus();
				return;
			}

			if (e.key === 'Enter' && isActivePassword) {
				emailRef.current.blur();
				passwordRef.current.focus();

				return;
			}

			if (e.key === 'Escape') {
				handleBack();
			}

			if (e.key === 'ArrowUp') {
				emailRef.current.focus();
			}

			if (e.key === 'ArrowDown') {
				passwordRef.current.focus();

				if (isActivePassword) {
					passwordRef.current.blur();
					emailRef.current.blur();
				}
			}
		}
	};

	const handleOpeningShift = (values) => {
		dispatch({
			type: 'transaction/OPENING_SHIFT',
			payload: {
				refDate: property?.refDate || '',
				beginCash: values?.openingCash || 0
				// shiftId: cashier?.shiftId
			},
			history
		});
	};

	const handleBack = (isActiveEmail, isActivePassword) => {
		if (isActiveEmail || isActivePassword) {
			form.resetFields();
			emailRef.current.focus();
		}

		return history.push('/pos/options');
	};

	useEffect(() => {
		document.addEventListener('keydown', onKeyDown);

		dispatch({
			type: 'auth/SET_STATE',
			payload: {
				isGrantedAccess: false
			}
		});

		return () => {
			document.removeEventListener('keydown', onKeyDown);
		};
	}, [form]);

	useEffect(() => {
		if (cashier && cashier?.isOpening) {
			return history.push('/pos/kasir');
		}
	}, []);

	return (
		<div className="w-full h-screen">
			<div className="w-full h-full flex flex-col items-center justify-center">
				<div className="max-w-xl">
					<div>
						<h1 className="text-2xl font-medium">Opening Shift</h1>
						<p className="text-base leading-snug mt-3">Mohon untuk membuka Shift terlebih dahulu sebelum melakukan transaksi.</p>
					</div>

					{isGrantedAccess ? (
						<div className="w-full h-auto mt-12">
							<Form
								form={form}
								name="opening"
								layout="vertical"
								initialValues={{
									remember: true
								}}
								onFinish={onFinish}
								onFinishFailed={onFinishFailed}
								autoComplete="off"
								style={{ maxWidth: 'none', width: '100%' }}>
								<Form.Item>
									<Form.Item
										label="Saldo Awal"
										name="openingCash"
										rules={[
											{
												required: true,
												message: 'Tambahkan saldo awal.'
											}
										]}>
										<Input
											defaultValue={0}
											ref={openingRef}
											type="number"
											autoFocus
											autoComplete="false"
											placeholder="0"
											prefix="IDR "
											className="w-full h-14 rounded-md"
										/>
									</Form.Item>
								</Form.Item>
								<div className="w-full flex items-center justify-start gap-2 mt-3">
									<Form.Item>
										<Button disabled={loadingTrx} loading={loadingTrx} htmlType="submit" className="btn-page">
											Buka Kasir
										</Button>
									</Form.Item>
									<Form.Item>
										<Button
											disabled={loadingTrx}
											htmlType="button"
											className="btn-page bg-transparent text-gray-700"
											onClick={() => history.push('/pos/options')}>
											Kembali
										</Button>
									</Form.Item>
								</div>
							</Form>
						</div>
					) : (
						<div className="w-3/4 my-7">
							<Form
								form={form}
								name="login"
								layout="vertical"
								initialValues={{
									remember: true
								}}
								onFinish={onFinish}
								onFinishFailed={onFinishFailed}
								autoComplete="off"
								style={{ maxWidth: 'none', width: '100%' }}>
								<Row wrap gutter={[8, 0]}>
									<Col span={24}>
										<Form.Item
											label="Email"
											name="email"
											rules={[
												{
													required: true,
													message: 'Wajib Diisi!'
												}
											]}>
											<Input
												ref={emailRef}
												type="email"
												autoFocus
												autoComplete="false"
												placeholder="usertest@domain.com"
												className="w-full h-12 rounded-md"
											/>
										</Form.Item>
									</Col>
									<Col span={24}>
										<Form.Item
											label="Password"
											name="password"
											rules={[
												{
													required: true,
													message: 'Wajib Diisi!'
												}
											]}>
											<Input.Password
												ref={passwordRef}
												type="password"
												autoComplete="false"
												placeholder="********"
												className="w-full h-12 rounded-md"
											/>
										</Form.Item>
									</Col>
									<Col span={24}>
										<div className="w-full flex items-center justify-start gap-2 mt-3">
											<Form.Item>
												<Button disabled={loading} loading={loading} type="primary" htmlType="submit" className="btn-page">
													validasi <kbd className="kbd">enter</kbd>
												</Button>
											</Form.Item>
											<Form.Item>
												<Button
													onClick={handleBack}
													disabled={loading}
													loading={loading}
													type="primary"
													className="btn-page bg-transparent text-gray-700">
													kembali <kbd className="kbd">esc</kbd>
												</Button>
											</Form.Item>
											{/* <Form.Item>
												<Button
													disabled={loading}
													type="default"
													htmlType="button"
													className="btn-page bg-transparent text-gray-700"
													onClick={() => history.push('/pos/auth')}>
													LOGOUT <kbd className="kbd">esc</kbd>
												</Button>
											</Form.Item> */}
										</div>
									</Col>
								</Row>
							</Form>
						</div>
					)}
				</div>
			</div>
		</div>
	);
}
